<template>
  <div>
    <button id="add-html" @click="addWin('html')"><i class="material-icons">add</i>HTML</button>
    <button id="add-css" @click="addWin('css')"><i class="material-icons">add</i>CSS</button>
    <button id="add-js" @click="addWin('javascript')"><i class="material-icons">add</i>JS</button>
    <button id="add-vue" @click="addWin('vue')"><i class="material-icons">add</i>VUE</button>
    <button id="add-sass" @click="addWin('sass')"><i class="material-icons">add</i>SASS</button>
    <CdCode v-for="(win, i) in wins" :key="i" class="code" v-model="win.code" :language="win.lang" title="Code Editor" draggable writeable closeable></CdCode>
  </div>
</template>

<script>
import Vue from 'vue'
import CdCode from '@/components/Cdcode'
import autosize from 'autosize'

export default {
  name: 'app',
  components: {
    CdCode
  },
  data () {
    return {
      wins: [],
      testCode: 'var i = 20;'
    }
  },
  methods: {
    addWin (lang) {
      this.wins.push({
        lang: lang,
        code: ''
      })
    }
  }
}
</script>

<style lang="css">
  html, body {
    padding: 0;
    margin: 0;
    width: 100%;
    height: 100%;
    background: LightCoral;
  }
  *,
  *:before,
  *:after {
    box-sizing: border-box;
  }
  *:focus {
    outline: none;
  }
  a {
    text-decoration: none;
    color: inherit;
  }
  .code {
    width: 40% !important;
  }

  .test {
    position: relative;
    width: 30%;
    min-height: 60px;
    height: auto;
    max-height: 400px;
    overflow-y: scroll;
    background: #EEE;
  }

  .test .content textarea {
    width: 100%;
    height: 100%;
    resize: none !important;
    border: none;
    padding: 0;
    margin: 0;
    position: absolute;
    top: 0;
    left: 0;
    bottom: 0;
    overflow-y: hidden;
    text-fill-color: transparent;
    -webkit-text-fill-color: transparent;
    background: rgba(0, 0, 255, .3);
    font-size: 20px;
    font-weight: 600;
    line-height: 1.5;
    z-index: 1;
    white-space: pre-wrap;
  }
  .test .content pre.highlight {
    position: absolute;
    color: lime;
    top: 0;
    left: 0;
    bottom: 0;
    padding: 0;
    margin: 0;
    height: 100%;
    width: 100%;
    font-size: 20px;
    font-weight: 600;
    line-height: 1.5;
    white-space: pre-wrap;
    word-wrap: break-word;
  }
  .test .content pre.invis {
    position: static;
    color: red;
    top: 0;
    left: 0;
    bottom: 0;
    padding: 0;
    margin: 0;
    height: 100%;
    width: 100%;
    font-size: 20px;
    font-weight: 600;
    line-height: 1.5;
    white-space: pre-wrap;
    word-wrap: break-word;
  }

</style>



