<template>
	<div class="window" v-draggable="draggable" v-if="show">
		<div class="window-header">
			<button id="copy-code" v-clipboard="code"><i class="material-icons" style="font-size: 40px">content_copy</i></button>
			<div class="title">{{ title }}</div>
			<button class="close" v-if="closeable" @click="show = false"><i class="material-icons">close</i></button>
			<div class="filler" v-else></div>
			<div class="language">{{ language }}</div>
		</div>
		<Scrollbar>
	    <div class="window-body">
				<div class="line-numbers">
					<span v-for="num in lines"><span>{{ num.num }}<span class="filler"></span><br></span><span v-for="numm in num.wraps">•<span class="filler"></span><br></span></span>
				</div>
				<div class="text-area">
					<textarea v-autosize ref="textArea" class="code-input" spellcheck="false" :readonly="!writeable" v-model="code" onkeydown="if(event.keyCode===9){var v=this.value,s=this.selectionStart,e=this.selectionEnd;this.value=v.substring(0, s)+'\t'+v.substring(e);this.selectionStart=this.selectionEnd=s+1;return false;}"></textarea>
		  		<pre class="highlight" :class="languageClass">
<code v-html="highlightedCode(language)"></code></pre>
		      <pre class="invis">
<code v-html="highlightedCode(language)+'\n'"></code></pre>
				</div>
	    </div>
		</Scrollbar>
	</div>
</template>

<script>
import Vue from 'vue'
import VueClipboards from 'vue-clipboards'
import Scrollbar from 'vue-perfect-scrollbar'

Vue.use(VueClipboards)

Vue.directive('autosize', {
  update (el) {
		setTimeout(() => {
			let heightEl = el.parentElement.lastChild
	    let height = heightEl.scrollHeight*1
	    el.style.height = height+'px'
		}, 100)
  }
})

Vue.directive('draggable', {
  bind: function (el, binding) {
		if (binding.value) {
			el.style.position = 'absolute';
	    var startX, startY, initialMouseX, initialMouseY;

	    function mousemove(e) {
	      var dx = e.clientX - initialMouseX;
	      var dy = e.clientY - initialMouseY;
	      el.style.top = startY + dy + 'px';
	      el.style.left = startX + dx + 'px';
	      return false;
	    }

	    function mouseup() {
	      document.removeEventListener('mousemove', mousemove);
	      document.removeEventListener('mouseup', mouseup);
	    }

			let elements = []
			setTimeout(()=>{
				let elements2 = document.getElementsByClassName('window')
				for (let i in elements2) {
					if (elements2[i].style != undefined) {
						elements2[i].style.zIndex = i*1+1
						elements.push(elements2[i])
					}
				}
			}, 100)

	    el.children[0].addEventListener('mousedown', function(e) {
				let topZindex = 1
				if (elements[0].style != undefined) {
					let sortedElements = elements.sort((a, b) => {
						return a.style.zIndex > b.style.zIndex ? 1 : -1
					})
					topZindex = sortedElements[0].style.zIndex
				}
				el.style.zIndex = topZindex*1 + 1
	      startX = el.offsetLeft;
	      startY = el.offsetTop;
	      initialMouseX = e.clientX;
	      initialMouseY = e.clientY;
	      document.addEventListener('mousemove', mousemove);
	      document.addEventListener('mouseup', mouseup);
	      return false;
	    });
		}
  }
})

export default {
  name: 'CdCode',
	components: { Scrollbar },
  props: {
    value: {
      type: String,
      default: ''
    },
    language: {
      type: String,
      default: 'html'
    },
    title: {
      type: String,
      required: true
    },
    writeable: {
      type: Boolean,
      default: false
    },
		draggable: {
			type: Boolean,
			default: false
		},
		closeable: {
			type: Boolean,
			default: false
		}
  },
  data () {
    let tmp2 = ''
    if (this.value != '') {
      let tmp = this.value.split('\n')
      tmp.splice(0, 1)
      tmp.splice(tmp.length-1)
      tmp2 = tmp.join('\n')
    }
    return {
      code: tmp2,
			show: true,
			ta: undefined,
			lineLength: 0
    }
  },
  computed: {
		lines () {
			let ta = this.ta
			let maxWidth = 12
			if (ta != undefined) {
				maxWidth = ta.clientWidth - 50
			}
			let code = this.code
			let lines = code.split('\n')
			let lineCount = []
			let lineLength = this.lineLength
			for (let i in lines) {
				lineCount[i] = { num: i*1+1 , wraps: 0}
				let wrap = maxWidth - (lines[i].length*12)
				if (wrap > 0 && wrap < 12) {
					this.lineLength = lines[i].length
					lineLength = this.lineLength
				}
				if (lines[i].length % lineLength < lineLength) {
					let wraps = Math.floor(lines[i].length / lineLength)
					lineCount[i].wraps = wraps
				}
			}
			return lineCount
		},
		languageClass () {
			let lang = this.language
			if (lang == 'vue') {
				return 'html css javascript'
			}
			else if (lang == 'scss' || lang == 'less' || lang == 'sass') {
				return 'css'
			}
			else {
				return lang
			}
		}
  },
  methods: {
    highlightedCode (lang) {
      let code = this.code
			code = code.replace(/(<|>)/g, (el, tag)=> {
				if (tag == '<') {
					return '&lt'
				}
				if (tag == '>') {
					return '&gt'
				}
			})
      if (lang == 'html') {
        let html = code
        html = html.replace(/&lt(.+?)&gt/g, this.styleHTML)
        html = html.replace(/<span class="tag-punct">&lt<\/span><span class="tag">(!--[\s\S]*?--)<\/span><span class="tag-punct">&gt<\/span>/g, (el, code) => {
          return '<span class="comment">&lt'+code.replace(/<span class=".+?">|<\/span>/g, '')+'&gt</span>'
        })
				html = html.replace(/(#([0-9abcdefABCDEF]{3}){1,2})/g, this.colorHEX)
        return html
      }
      else if (lang == 'javascript') {
        let html = code
        html = html.replace(/.+/g, this.styleJS)
				html = html.replace(/(#([0-9abcdefABCDEF]{3}){1,2})/g, this.colorHEX)
        return html
      }
			else if (lang == 'css' || lang == 'scss' || lang == 'less') {
				let html = code
				html = html.replace(/[\s\S]+/g, this.styleCSS)
				html = html.replace(/(#([0-9abcdefABCDEF]{3}){1,2})/g, this.colorHEX)
				return html
			}
			else if (lang == 'sass') {
				let html = code
				html = html.replace(/[\s\S]+/g, this.styleSASS)
				html = html.replace(/(#([0-9abcdefABCDEF]{3}){1,2})/g, this.colorHEX)
				return html
			}
			else if (lang == 'vue') {
				let html = code
				html = html.replace(/(&lttemplate&gt)([\s\S]+)(&lt\/template&gt)\n{1,2}(&ltscript&gt)([\s\S]+)(&lt\/script&gt)\n{1,2}(&ltstyle(.*)&gt)([\s\S]+)(&lt\/style&gt)/g, this.styleVUE)
				html = html.replace(/(#([0-9abcdefABCDEF]{3}){1,2})/g, this.colorHEX)
				return html
			}
      else {
        return code
      }
    },
    styleHTML (el, tag) {
      let tagPunct1 = '<span class="tag-punct">&lt</span>'
      let tagPunct2 = '<span class="tag-punct">&gt</span>'
      let tagName = '<span class="tag">'+tag.split(' ')[0]+'</span>'
      let attributes = ''
      if (tag == '!DOCTYPE html') {
        tagName = '<span class="doctype">'+tag+'</span>'
      }
      if (tag.split(' ')[1] && tag != '!DOCTYPE html') {
        let c = tag.split(/ /)
        let c2 = ''
        for (let i = 1; i < c.length; i++) {
          c2 = c2 + ' ' + c[i]
        }
        c2 = c2.replace(/(")(.+?)(")/g, '<span class="punct">$1</span><span class="string">$2</span><span class="punct">$3</span>')
        c2 = c2.replace(/(.+?)(=)(<span)/g, '$1<span class="punct">$2</span>$3')
        attributes = '<span class="attribute">'+c2+'</span>'
      }
      return tagPunct1+tagName+attributes+tagPunct2
    },
    styleJS (el) {
      let styled = el
      if (/(\/\/.+)/g.test(el)) {
        styled = el.replace(/\/\/.+/g, '<span class="comment">$&</span>')
      } else {
				styled = styled.replace(/([0-9]+)/g, '<span class="number">$1</span>')
	      styled = styled.replace(/(var|let|const|Boolean|String|Number|Object|Array)/g, '<span class="type">$1</span>')
	      styled = styled.replace(/(import|new |return|function|export default|export|from| in |for |if |else )/g, '<span class="function">$1</span>')
	      styled = styled.replace(/(true)/g, '<span class="true">$1</span>')
	      styled = styled.replace(/(false)/g, '<span class="false">$1</span>')
	      styled = styled.replace(/(.+?) \((.*)\)(?= {)/g, '<span class="function">$1</span> (<span class="args">$2</span>)')
	      styled = styled.replace(/([;:,.!^*_+\-=|?(){}\[\]])(?!['"])/g, '<span class="punct">$1</span>')
	      styled = styled.replace(/ (?!(class=))(['`])(.*?)['`]/g, '<span class="string"> <span class="punct">$2</span>$3<span class="punct">$2</span></span>')
	      styled = styled.replace(/(.+)/g, '<span class="var">$1</span>')
			}
      return styled
    },
		styleCSS (el) {
			let styled = el
			styled = styled.replace(/((\.|#|\w).+?(?= {))( {)([\s\S]+)([\s]})/g, (el, tag, d, punct1, attributes, punct2) => {
				let attrs = attributes.replace(/(.+: )(.+(?=;))(;)/g, (el, attr, value, punct) => {
					return '<span class="attribute">'+attr+'</span><span class="value">'+value.replace(/(px|rem|em|%|vh|vw|fr)/g, '<span class="mod">$1</span>')+'</span><span class="punct">'+punct+'</span>'
				})
				return '<span class="tag">'+tag+'</span><span class="punct">'+punct1+'</span>'+attrs+'<span class="punct">'+punct2+'</span>'
			})
			return styled
		},
		styleSASS (el) {
			let styled = '<span class="tag">'+el+'</span>'
			styled = styled.replace(/(@.+ )('.+')/g, '<span class="tag">$1</span><span class="value">$2</span>')
			styled = styled.replace(/(\s.+: (.+))/g, (el, attribute, value) => {
				return '<span class="attribute">'+attribute.replace(value, '<span class="value">'+value.replace(/(px|rem|em|%|vh|vw|fr)/g, '<span class="mod">$1</span>')+'</span>')+'</span>'
			})
			return styled
		},
		styleVUE (el, template1, html, template2, script1, js, script2, style1, cssLang, css, style2) {
			let styled = el
			let cssLanguage = ''
			if (cssLang) {
				cssLanguage = cssLang.replace(/ lang="(.+)"/g, '$1')
			} else {
				cssLanguage = 'css'
			}
			styled = styled.replace(template1, (el) => { return el.replace(/&lt(.+?)&gt/g, this.styleHTML) })
			styled = styled.replace(template2, (el) => { return el.replace(/&lt(.+?)&gt/g, this.styleHTML) })
			styled = styled.replace(script1, (el) => { return el.replace(/&lt(.+?)&gt/g, this.styleHTML) })
			styled = styled.replace(script2, (el) => { return el.replace(/&lt(.+?)&gt/g, this.styleHTML) })
			styled = styled.replace(style1, (el) => { return el.replace(/&lt(.+?)&gt/g, this.styleHTML) })
			styled = styled.replace(style2, (el) => { return el.replace(/&lt(.+?)&gt/g, this.styleHTML) })
			styled = styled.replace(html, (el) => { return el.replace(/&lt(.+?)&gt/g, this.styleHTML) })
			styled = styled.replace(js, (el) => { return el.replace(/.+/g, this.styleJS) })
			if (cssLanguage == 'css' || cssLanguage == 'scss' || cssLanguage == 'less') {
				styled = styled.replace(css, (el) => { return el.replace(/[\s\S]+/g, this.styleCSS) })
			}
			if (cssLanguage == 'sass') {
				styled = styled.replace(css, (el) => { return el.replace(/[\s\S]+/g, this.styleSASS) })
			}
			return styled
		},
		colorHEX (el) {
			let hex = el
			if (/#[0-9abcdefABCDEF]{3}$/g.test(el)) {
				hex = hex.replace(/([0-9abcdefABCDEF]{1})/g, '$1$1')
			}
			function getHEXlightness (color) {
				var result = /^#?([a-f\d]{2})([a-f\d]{2})([a-f\d]{2})$/i.exec(hex);
		    var r = parseInt(result[1], 16);
		    var g = parseInt(result[2], 16);
		    var b = parseInt(result[3], 16);
		    r /= 255, g /= 255, b /= 255;
		    var max = Math.max(r, g, b), min = Math.min(r, g, b);
		    var l = (max + min) / 2;
		    l = Math.round(l*100);
				return l
			}
			if (getHEXlightness(hex) < 50) {
				return '<span class="color dark" style="background: '+el+'">'+el+'</span>'
			} else {
				return '<span class="color light" style="background: '+el+'">'+el+'</span>'
			}
		}
  },
	mounted () {
		this.ta = this.$refs.textArea
	}
}
</script>

<style lang="css">
  .window {
    width: 100%;
    height: auto;
    border-radius: 3px;
    position: relative;
    background: #F0F0F0;
    overflow: auto;
    box-shadow: 0 5px 10px 0px rgba(0, 0, 0, 0.4);
		z-index: 5;
  }

  .window-header {
    height: 40px;
    background: #DCDCDC;
		color: black;
  }
	.window-header #copy-code {
		border-radius: 50%;
		margin: 0;
		padding: 0;
		border: none;
		box-shadow: none;
		background: transparent;
		z-index: 4;
		position: absolute;
		right: 3px;
		top: 40px;
		padding: 10px;
		opacity: .3;
		transition: opacity .3s ease;
		cursor: pointer;
	}
	.window-header #copy-code:hover {
		opacity: .6;
	}
  .window-header .title {
    float: left;
    font-size: 16px !important;
    font-weight: 600;
    margin: 0;
    position: relative;
    top: 12px;
    left: 12px;
    opacity: .7;
  }
  .window-header .language {
    float: right;
    font-size: 16px;
    font-weight: 600;
    margin: 0;
    position: relative;
    top: 12px;
    right: 5px;
    opacity: .7;
		text-transform: uppercase;
  }
	.window-header .close {
		width: 36px;
		height: 36px;
		margin: 0;
		margin-top: 2px;
		margin-right: 2px;
		padding: 0;
		border: none;
		float: right;
		background: transparent;
		cursor: pointer;
		border-radius: 20px;
		transition: background .3s ease;
	}
	.window-header .close:hover {
		background: rgba(0, 0, 0, .1);
	}
	.window-header .close i {
		float: right;
		font-size: 20px;
		font-weight: 600;
		margin: 0;
		position: relative;
		top: 1px;
		right: 8px;
		opacity: .7;
	}
	.window-header .filler {
		width: 7px;
		height: 40px;
		float: right;
	}

  .window-body {
    position: relative;
		width: 100%;
    min-height: 80px;
    height: auto;
    max-height: 600px;
    background: #F0F0F0;
  }

	.window-body .line-numbers {
		width: 72px;
		position: absolute;
		top: 0;
		left: 0;
		text-align: right;
		font-family: monospace;
		font-size: 20px;
		font-weight: 600;
		padding: 20px 0;
		line-height: 1.5;
		opacity: .6;
		color: rgba(38, 50, 56, 0.2);
	}
	.window-body .line-numbers span {
		padding-left: .5em;
	}
	.window-body .line-numbers span .filler {
		padding: 0 .4em;
		visibility: hidden;
	}

	.window-body .line-numbers span .filler:before {
		font-family: 'Octicons Regular';
    font-weight: normal;
    font-style: normal;
    display: inline-block;
    line-height: 1;
    -webkit-font-smoothing: antialiased;
    text-decoration: none;
    font-size: 0.2em;
    width: 0.2em;
    height: 0.2em;
    content: "\f0a3";
	}

	.window-body .text-area {
		width: calc(100% - 72px);
		position: relative;
		left: 72px;
	}

  .window-body pre.highlight {
    position: absolute;
    top: 0;
    left: 0;
    bottom: 0;
    padding: 0;
    margin: 0;
    height: 100%;
    width: 100%;
		font-family: monospace;
    font-size: 20px;
    font-weight: 600;
    line-height: 1.5;
    white-space: pre-wrap;
    word-wrap: break-word;
		padding: 20px;
		padding-left: 0;
		padding-right: 60px;
		tab-size: 1;
  }
  .window-body pre.invis {
    position: static;
    top: 0;
    left: 0;
    bottom: 0;
    padding: 0;
    margin: 0;
    height: 100%;
    width: 100%;
		font-family: monospace;
    font-size: 20px;
    font-weight: 600;
    line-height: 1.5;
    white-space: pre-wrap;
    word-wrap: break-word;
		padding: 20px;
		padding-left: 0;
		padding-right: 60px;
		opacity: 0;
		tab-size: 1;
  }

  .window-body textarea {
    width: 100%;
    height: 100%;
    resize: none !important;
    border: none;
    padding: 0;
    margin: 0;
    position: absolute;
    top: 0;
    left: 0;
    bottom: 0;
    overflow-y: hidden;
    text-fill-color: transparent;
    -webkit-text-fill-color: transparent;
		background: transparent;
		font-family: monospace;
    font-size: 20px;
    font-weight: 600;
    line-height: 1.5;
    z-index: 1;
    white-space: pre-wrap;
		padding: 20px;
		padding-right: 60px;
		padding-left: 0;
		tab-size: 1;
  }
  .window-body textarea::-webkit-input-placeholder {
    color: black;
  }
  .window-body textarea:focus {
    outline: 0;
    border: 0;
    box-shadow: none;
  }

  .link {
    text-decoration: underline;
  }

  .html .comment {
    color: #19B357;
		font-style: italic;
  }
  .html .tag,
  .html .tag-punct {
    color: #ab47bc;
  }
  .html .doctype {
    color: #666666;
  }
  .html .attribute {
    color: #2196f3;
  }
  .html .punct {
    color: #F9A825;
  }
  .html .string {
    color: #e57373;
  }

  .javascript .comment {
    color: #19B357;
		font-style: italic;
  }
  .javascript .number {
    color: #19B357;
  }
  .javascript .function {
    color: #ab47bc;
  }
  .javascript .args {
    color: #e57373;
  }
  .javascript .string {
    color: #e57373;
  }
  .javascript .punct {
    color: #5D65D5;
  }
  .javascript .type {
    color: #F9A825;
  }
  .javascript .true {
    color: #19B357;
  }
  .javascript .false {
    color: #EC464F;
  }
  .javascript .var {
    color: #2196f3;
  }

	.css .color.dark {
		color: white;
		border-radius: 3px;
	}
	.css .color.light {
		color: black;
		border-radius: 3px;
	}
	.css .tag {
		color: #ab47bc;
	}
	.css .punct {
		color: #5D65D5;
	}
	.css .attribute {
		color: #2196f3;
	}
	.css .value {
		color: #E57373;
	}
	.css .mod {
		color: #F9A725;
	}

</style>



